import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Observable } from 'rxjs';
import { ThemeChangeService } from '../services/theme-change/theme-change.service';
import { LocalStorageService } from '../services/local-storage/local-storage.service';

@Component({
  selector: 'app-side-bar',
  templateUrl: './side-bar.component.html',
  styleUrls: ['./side-bar.component.scss']
})
export class SideBarComponent implements OnInit {
  @Input() categories: Array<string>;
  @Input() isHandset$: Observable<boolean>;
  @Input() tags: Array<string>;

  useDarkTheme: boolean;

  constructor(private themeChangeService: ThemeChangeService) {}

  ngOnInit(): void {
    this.themeChangeService.useDarkTheme$.subscribe((bool) => {
      this.useDarkTheme = bool;
    });
  }

  changeTheme() {
    this.useDarkTheme = !this.useDarkTheme;
    this.themeChangeService.changeTheme();
  }
}
