import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { BlogPostService } from 'src/app/services/blog-post/blog-post.service';
import { Observable } from 'rxjs';
import { ScullyRoute, ScullyRoutesService } from '@scullyio/ng-lib';
import { Router } from '@angular/router';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { map, shareReplay, tap } from 'rxjs/operators';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  homePosts$: Observable<ScullyRoute[]>;
  isHandset$: Observable<boolean> = this.breakpointObserver
    .observe(Breakpoints.Handset)
    .pipe(
      tap(result => console.log(result)),
      map(result => result.matches),
      shareReplay()
    );

  constructor(
    public breakpointObserver: BreakpointObserver,
    public router: Router,
    public postService: BlogPostService
  ) {}

  ngOnInit(): void {
    this.homePosts$ = this.postService.homePosts$;
  }

  navigateToRoute(routePath: string, route: ScullyRoute) {
    this.router.navigate([routePath], { state: { route } });
  }

  navigateToUrl(url: string) {
    window.location.href = url;
  }
}
