import { Component } from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { BlogPostService } from '../services/blog-post/blog-post.service';
import { ScullyRoute, ScullyRoutesService } from '@scullyio/ng-lib';
import { ThemeChangeService } from '../services/theme-change/theme-change.service';

@Component({
  selector: 'app-main-nav',
  templateUrl: './main-nav.component.html',
  styleUrls: ['./main-nav.component.scss']
})
export class MainNavComponent {
  homePosts$: Observable<Array<ScullyRoute>>;
  isHandset$: Observable<boolean> = this.breakpointObserver
    .observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches),
      shareReplay()
    );
  useDarkTheme$ = this.themeChangeService.useDarkTheme$;

  constructor(
    private breakpointObserver: BreakpointObserver,
    public routerService: ScullyRoutesService,
    private themeChangeService: ThemeChangeService,
    private blogPostService: BlogPostService
  ) {
    this.homePosts$ = this.blogPostService.homePosts$;
  }
}
