import { Injectable } from '@angular/core';
import { ScullyRoutesService, ScullyRoute } from '@scullyio/ng-lib';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class BlogPostService {
  available$: Observable<ScullyRoute[]>;
  categories: Array<string>;
  tags: Array<string> = [];
  homePosts$: Observable<ScullyRoute[]>;

  constructor(public scullyRoutesService: ScullyRoutesService) {
    this.available$ = this.scullyRoutesService.available$;
    this.homePosts$ = this.available$.pipe(
      map((posts: ScullyRoute[]) => {
        let filtered = posts.filter(post => post.title !== undefined);
        filtered = filtered.filter(post => post.draft !== true);
        return filtered;
      }),
      map((posts: ScullyRoute[]) => {
        let cats: Array<string> = [];
        let tags: Array<string> = [];
        posts.forEach(post => {
          if (post.tags) {
            tags = tags.concat(post.tags);
            tags = tags.reduce((x, y) => (x.includes(y) ? x : [...x, y]), []);
            this.tags = this.sortArray(tags);
          }
          if (post.categories) {
            cats = cats.concat(post.categories);
            cats = cats.reduce((x, y) => (x.includes(y) ? x : [...x, y]), []);
            this.categories = this.sortArray(cats);
          }
        });
        return posts;
      }),
      map((posts: ScullyRoute[]) => {
        posts.sort((a, b) => {
          return a.date < b.date ? 1 : -1;
        });
        return posts;
      })
    );
  }

  private sortArray(arr: Array<string>): Array<string> {
    arr.sort((a, b) => {
      return a.toLowerCase() < b.toLowerCase() ? -1 : 1;
    });

    return arr;
  }
}
