import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { LocalStorageService } from '../local-storage/local-storage.service';

@Injectable({
  providedIn: 'root'
})
export class ThemeChangeService {
  useDarkTheme = false;
  useDarkTheme$: BehaviorSubject<boolean> = new BehaviorSubject(
    this.useDarkTheme
  );

  constructor(private localStorageService: LocalStorageService) {
    const bool = JSON.parse(localStorageService.getItem('dark-theme'));
    this.useDarkTheme = !bool ? false : bool;
    this.useDarkTheme$.next(this.useDarkTheme);
    this.localStorageService.setItem('dark-theme', this.useDarkTheme);

  }

  changeTheme() {
    this.useDarkTheme = !this.useDarkTheme;
    this.useDarkTheme$.next(this.useDarkTheme);
    this.localStorageService.setItem('dark-theme', this.useDarkTheme);
  }
}
